.banner {
    height: 840px;
    background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner.png');
    position: relative;

    p {
        color: $secondary;
        position: absolute;

        &.series {
            top: 350px;
            right: 396px;
            font-size: 36px;
            line-height: 50px;
        }

        &.slogan {
            top: 400px;
            right: 405px;
            font-size: 64px;
            line-height: 90px;
            font-weight: bold;
        }
    }
}

.p-block {
    padding: 64px 0 80px 0;
}

.profession {
    background-color: $light;
    color: $secondary;
    
    .container {
        display: flex;
        justify-content: space-between;
    }

    .profession-item {
        width: 208px;
        
        .img-wrap {
            height: 100px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 40px;
        }
    }

    .profession-item-title {
        text-align: center;
        font-size: 24px;
        line-height: 33px;
        font-weight: bold;
        margin-bottom: 4px;
    }

    .profession-item-content {
        line-height: 24px;
        color: #000;
    }
}

h2.title {
    text-align: center;
    font-size: 48px;
    line-height: 67px;
    color: $secondary;
    font-weight: bold;
    margin-bottom: 60px;
}

.classic {
    color: $primary;

    .img-wrap {
        height: 320px;
        width: 650px;
    }

    .optical {
        .classic-title-icon {
            width: 48px;
            height: 48px;
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/icon-diamond.svg');
        }
    }

    .optical, .functional {
        display: flex;
        justify-content: center;
        margin-bottom: 32px;

        .img-wrap {
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-1.png');
        }

        .classic-title {
            margin-left: 30px;
        }
    }

    .classic-title {
        font-size: 48px;
        line-height: 63px;
        font-weight: 900;
        font-style: italic;
        display: flex;
        align-items: center;

        .classic-title-icon {
            margin-right: 8px;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }
    }

    .sunglasses {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        margin-bottom: 32px;

        .img-wrap {
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-2.png');
        }
        
        .classic-title {
            justify-content: flex-end;

            .classic-title-icon {
                width: 48px;
                height: 48px;
                background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/icon-sun.svg');
            }
        }
    }

    .functional{
        .classic-title-icon {
            width: 35.2px;
            height: 48px;
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/icon-thunder.svg');
        }

        .img-wrap {
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-3.png');
        }
    } 
}

.design {
    background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/bg.png');

    .title {
        color: #fff;
    }

    .container {
        display: flex;
        justify-content: space-between;
    }

    .double, .youth {
        width: 540px;
        height: 472px;
        position: relative;
        color: $primary;


        &::before {
            content: '';
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-color: rgba(255, 255, 255, 0.5);
            font-size: 72px;
            line-height: 95px;
            font-weight: bold;
        }
    }

    .double {
        background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-8.png');
        
        &::before {
            content: 'DOUBLE A+';
        }
    }

    .youth {
        background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-9.png');
        
        &::before {
            content: 'YOUTH';
        }
    }
}

.recommend {
    .container {
        display: flex;
        justify-content: space-between;
    }

    .recommend-item {
        width: 255px;
        border: 1px solid #DEE2E6;

        .img-wrap {
            height: 256px;
        }
    }

    .recommend-item-content {
        padding: 14px 16px;

        .recommend-item-content-name {
            font-size: 14px;
            line-height: 19px;
            font-weight: medium;
            margin-bottom: 7px;
        }

        .recommend-item-content-description {
            line-height: 24px;
            margin-bottom: 10px;
        }

        .recommend-item-content-time {
            color: #707070;
            font-size: 14px;
            line-height: 19px;
        }
    }
}

.contactUs {
    background-color: $light;

    .title {
        color: #000;
        margin-bottom: 24px;
    }

    .container {
        max-width: 730px;
    }

    .annotation {
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 20px;
    }

    .contactUs-form {

        .contactUs-form-group {
            margin-bottom: 16px;
            label {
                display: block;
                line-height: 22px;
                margin-bottom: 4px;
            }
    
            input, textarea {
                display: block;
                width: 100%;
                box-sizing: border-box;
                padding: 14px 16px;
                border: 1px solid #000;
                line-height: 22px;
                background: transparent;
            }
        }

        .contactUs-form-checkbox {
            margin-bottom: 42px;
            display: flex;
            align-items: center;

            input[type="checkbox"] {
                width: 18px;
                height: 18px;
                box-sizing: border-box;
                margin: 3px !important;
            }

            label {
                line-height: 22px;
            }
        }

        .contactUs-form-btn-wrap {
            text-align: center;
            .contactUs-form-btn {
                padding: 16px 143px;
                border-radius: none;
                background-color: $primary;
                color: #fff;
                border: none;
            }
        }
        
    }
}

@media (max-width: 769px) {
    .p-block {
        padding: 40px 0;
    }

    .banner p {
        &.series {
            top: 372px;
            right: 39px;
            font-size: 20px;
            line-height: 28px;
        }

        &.slogan {
            top: 400px;
            right: 39px;
            font-size: 48px;
            line-height: 67px;
        }
    }

    .profession {

        .container {
            flex-wrap: wrap;
            justify-content: space-evenly;
        }
        
        .profession-item {
            width: 237px;
            margin-bottom: 40px;

            .profession-item-title {
                font-size: 20px;
                line-height: 28px;
                margin-bottom: 8px;
            }

            .img-wrap {
                height: 80px;
                margin-bottom: 16px;

                img {
                    width: 80px;
                }
            }
        }
    }

    h2.title {
        margin-bottom: 32px;
    }

    .classic {
        .classic-title {
            font-size: 28px;
            line-height: 37px;
            padding-left: 30px;
        }

        .img-wrap {
            height: 168px;
            width: 390px;
        }

        .optical, .sunglasses, .functional {
            justify-content: flex-start;
            .classic-title {
                margin-left: 0;
            }
        }

        .optical .classic-title .classic-title-icon {
            width: 32px;
            height: 25.16px;
        }

        .sunglasses .classic-title .classic-title-icon {
            width: 32px;
            height: 32px;
        }

        .functional .classic-title .classic-title-icon {
            width: 23px;
            height: 32px;
        }

        .optical, .sunglasses {
            margin-bottom: 8px;
        }

        .sunglasses {
            flex-direction: row;

            .classic-title {
                justify-content: flex-start;
            }
        }
    }

    .design {
        .double, .youth {
            width: 330px;
            height: 280px;

            &::before {
                font-size: 32px;
                line-height: 43px;
            }
        }
    }

    .recommend {
        padding-bottom: 10px;
        .container {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .recommend-item {
            width: 330px;    
            margin-bottom: 30px;
        }
    }

    .contactUs {
        .container {
            max-width: 450px;
        }

        .title {
            margin-bottom: 16px;
        }

        .annotation {
            font-size: 16px;
            line-height: 24px;
            margin-bottom: 12px;
        }

        .contactUs-form {

            .contactUs-form-group {
                margin-bottom: 8px;
                label {
                    display: block;
                    line-height: 20px;
                    margin-bottom: 4px;
                }
        
                input, textarea {
                    padding: 8px 12px;
                    line-height: 20px;
                }
            }

            .contactUs-form-checkbox {
                font-size: 14px;
                line-height: 21px;
                align-items: flex-start;

                input[type="checkbox"] {
                    width: 16px;
                    height: 16px;
                    margin: 1.63px !important;
                }
            }

            .contactUs-form-btn-wrap {
                .contactUs-form-btn {
                    padding: 8px 76px;
                    font-size: 14px;
                    line-height: 20px;
                }
            }
        }
    }
}

@media (max-width: 376px) {
    .banner {
        height: 394px;
        background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner-sm.png');    
        p {
            &.series {
                top: 164px;
                right: 15px;
                font-size: 16px;
                line-height: 22px;
            }

            &.slogan {
                top: 186px;
                right: 15px;
                font-size: 32px;
                line-height: 45px;
            }
        }
    }

    h2.title {
        font-size: 24px;
        line-height: 33px;
        margin-bottom: 20px;
    }

    .profession.p-block {
        padding-bottom: 12px;
    }

    .p-block {
        padding: 32px 0;
    }

    .classic {
        .img-wrap {
            height: 134px;
            width: 345px;
        }

        .optical .classic-title .classic-title-icon {
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/icon-diamond-white.svg');
        }

        .sunglasses .classic-title .classic-title-icon {
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/icon-sun-white.svg');
        }

        .functional .classic-title .classic-title-icon {
            background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/icon-thunder-white.svg');
        }

        .optical, .sunglasses, .functional {
            position: relative;
            justify-content: center;
            color: #fff;

            .classic-title {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                padding: 0;
                display: flex;
                justify-content: center;
            }
        }
    }

    .design {
        .container {
            flex-direction: column;
            align-items: center;

            .double {
                margin-bottom: 16px;
            }
        }
    }

    .recommend {
        .container {
            justify-content: center;
        }

        .recommend-item {
            width: 345px;
            margin-bottom: 16px;

            .img-wrap {
                height: 259px;
            }
        }
    }
    .contactUs {
        .container {
            max-width: 345px;
        }
    }
}