.category {
    margin: 0 auto;

    .category-navbar {
        display: flex;
        justify-content: center;

        .category-navbar-item {
            width: 462px;
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            padding: 24px 0 8px 0;
            line-height: 55px;
            font-size: 42px;
            color: #000;
            border: 1px solid #DCDCDC;
            border-bottom: 16px solid transparent;
            
            img {
                margin-right: 8px;
            }

            &.active, &:hover, &:focus {
                border-bottom: 16px solid $primary;
            }
        }
    }
}

.products-banner {
    display: flex;
    justify-content: space-between;
    
    img {
        display: block;
        height: auto;
        width: 50%;
    }
}

.products {
    padding: 88px 0 156px 0;

    .products-title {
        font-size: 42px;
        line-height: 63px;
        text-align: center;
        margin-bottom: 40px;
        font-weight: medium;

        small {
            font-size: 32px;
            line-height: 48px;
            font-weight: normal;
        }
    }

    .container {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 6px;
    }

    .products-item {
        width: 255px;
        text-transform: uppercase;
        font-size: 24px;
        line-height: 36px;
        margin-bottom: 104px;

        img {
            display: block;
            height: 164px;
            width: 100%;
        }

        .products-item-content {
            display: flex;
            justify-content: space-between;

            span {
                color: $primary;
            }
        }

        .products-item-color-wrap {
            display: flex;

            .products-item-color {
                width: 24px;
                height: 24px;
                display: block;
                border-radius: 4px;
    
                &:nth-child(1) {
                    background-color:#5F3E2D;
                    margin-right: 8px;
                }
    
                &:nth-child(2) {
                    background-color:#B75929;
                }
            }
        }

        
    }
}

.pagination {
    display: flex;
    justify-content: center;

    .pagination-list {
        display: flex;

        .pagination-list-item {
            display: inline-block;
            font-size: 18px;
            line-height: 24px;
            border: 1px solid #555555;
            text-decoration: none;
            color: #555555;
            padding: 10px 16px 14px 15px;

            &.controller {
                font-size: 19px;
                line-height: 26px;
                padding: 10px 12px 12px 12px;
                background-color: #F2F2F2;
            }

            &.active, &:hover, &:focus {
                background-color: #000;
                color: #fff;
            }
        }
    }
}

@media (max-width: 769px) {
    .category .category-navbar .category-navbar-item {
        width: 256px;
        font-size: 28px;
        line-height: 37px;
        padding-top: 16px;
        padding-bottom: 8px;
        border-bottom-width: 8px;

        img.icon-diamond, img.icon-sun {
            width: 32px;
        }

        img.icon-thunder {
            width: 23px;
        }

        &.active, &:hover, &:focus {
            border-bottom-width: 8px;
        }
    }

    .products {
        padding-top: 40px;
        padding-bottom: 104px;

        .products-title {
            font-size: 32px;
            line-height: 43px;
            margin-bottom: 32px;

            small {
                font-size: 24px;
                line-height: 33px;
            }
        }

        .container {
            justify-content: space-evenly;
            margin-bottom: 33px;
        }

        .products-item {
            width: 330px;
            margin-bottom: 72px;
        }
    }

}

@media (max-width: 376px) {
    .category .category-navbar .category-navbar-item {
        width: 125px;
        font-size: 14px;
        line-height: 19px;
        border-bottom-width: 4px;
        padding-top: 8px;
        padding-bottom: 4px;

        img.icon-diamond {
            width: 15px;
        }

        img.icon-sun {
            width: 14px;
        }

        img.icon-thunder {
            width: 10.77px;
        }

        &.active, &:hover, &:focus {
            border-bottom-width: 4px;
        }
    }

    .products {
        padding-top: 30px;
        padding-bottom: 56px;

        .container {
            margin-bottom: 8px;
        }

        .products-item {
            margin-bottom: 32px;
            width: 345px;
        }
    }

    .pagination .pagination-list .pagination-list-item{
        font-size: 11px;
        line-height: 15px;
        padding: 8px 10px 9px 11px;

        &.controller {
            font-size: 12px;
            line-height: 17px;
            padding: 8px 8px 7px 9px;
        }
    }
}