@import './helpers/reset';
$primary: #AA0601;
$secondary: #650300;
$light: rgba(170, 6, 1, 0.05);


body {
  box-sizing: border-box;
  font-family: 'Roboto' ,'Noto Sans', sans-serif;
}

.header {
  background-color: $primary;
  display: flex;
  padding: 30px 88px;
  justify-content: space-between;
  align-items: center;

  a.logo {
    display: inline-block;
    width: 80px;
    height: 48.57px;
    background-image: url('../../assets/images/logo-new_工作區域 1.png');
    background-size: cover;
    background-repeat: no-repeat;
    text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;
  }

  ul.menu {
    display: flex;
    width: 492px;
    justify-content: space-between;

    a {
      text-decoration: none;
      color: #fff;

      &:hover {
        color: #c8c8c8;
      }
    }
  }
}

.container {
  max-width: 1110px;
  margin: 0 auto;
}

.bg-cover {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.footer {
  background-color: $primary;
  padding: 32px 0 28.43px 0 ;

  .contact-wrap {
    display: flex;
    justify-content: space-between;
  }

  .contact {
    width: 492px;

    .footer-menu {
      display: flex;
      justify-content: space-between;
      line-height: 48px;

      a {
        color: #fff;
        text-decoration: none;

        &:hover {
          color: #c8c8c8;
        }
      }
    }

    .contact-method {
      padding: 16px 0 5px 0;

      li {
        display: flex;
        align-items: center;
        margin-bottom: 17px;
      }

      .img-wrap {
        width: 48px;
        margin-right: 12px;
      }
      

      a {
        font-size: 36px;
        line-height: 54px;
        text-decoration: none;
        color: #fff;

        &:hover {
          color: #c8c8c8;
        }
      }
    }
  }

  .social {
    width: 208px;
    
    .social-btn {
      display: flex;
      justify-content: space-between;

      img {
        width: 48px;
        height: 48px;
      }
    }
  }

  .right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    padding-top: 24px;
    border-top: 1px solid #fff;

    .footer-logo {
      img {
        width: 80px;
        height: 48.57px;
      }
      
    }

    .copyright {
      width: 992px;
      display: flex;
      justify-content: space-between;

      p {
        line-height: 24px;
      }

      .law {
        width: 188px;
        display: flex;
        justify-content: space-between;

        a {
          color: #fff;
          text-decoration: none;

          &:hover {
            color: #c8c8c8;
          }
        }
      }
    }
  }
}

@media (max-width: 769px) {
  .header {
    padding-left: 39px;
    padding-right: 39px;
  }

  .container {
    max-width: 690px;
  }

  .footer{
    .contact {
      width: 432px;
    }

    .social{
      width: 192px;
    }
  } 
}

@media (max-width: 376px) {
  .header {
    flex-direction: column;
    justify-content: start;
    padding: 0;

    a.logo {
      width: 48px;
      height: 29.14px;
      margin-top: 10px;
      margin-bottom: 10.86px;
    }

    ul.menu {
      flex-wrap: wrap;
      width: 100%;      

      li {
        box-sizing: border-box;
        width: 50%;
        text-align: center;
        padding: 8px;
        font-size: 20px;
        line-height: 30px;
        border: 1px solid #fff;
      }
    }
  }

  .footer {
    padding: 23px 15px 17px 15px;

    .contact {

      .footer-menu {
        display: none;
      }

      .contact-method {
        width: 194px;
        padding-top: 0;
        
        li {
          margin-bottom: 7px;
        }

        .img-wrap {
          width: 20px;

          img {
            width: 20px;
            height: 20px;
          }
        }

        a {
          font-size: 16px;
          line-height: 21px;
        }
      }
    }
    .social {
      .social-btn {
        width: 120px;

        li {
          width: 20px;

          img {
            width: 20px;
            height: 20px;
          }
        }
      }
    }

    .right {
      padding-top: 18px;
      flex-direction: row-reverse;
      align-items: flex-end;

      a.footer-logo {
        
        img {
          width: 46px;
          height: 27.93px;
        }
        
      }

      .copyright {
        flex-direction: column;

        p {
          font-size: 14px;
          margin-bottom: 7px;
        }

        ul.law {
          flex-direction: column;
          font-size: 14px;
          line-height: 20px;

          li {
            margin-bottom: 8px;
          }
        }
      }
    }
  }
}

@import './helpers/home';
@import './helpers/products';
